
.input-areas input {
    display: block;
    margin: 2rem auto;
    padding: .5rem 2rem;
    border-radius: 5px;
    font-size: 1.1rem;
    text-align: center;
}

.footer-container {
    background: linear-gradient(90deg, rgb(70, 60, 128) 0%, rgb(24, 23, 26) 100%);
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .footer-subscription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
  }
  
  .footer-subscription > p {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .footer-subscription-heading {
    margin-bottom: 24px;
    font-size: 24px;
  }
  
  .footer-subscription-text {
    margin-bottom: 24px;
    font-size: 20px;
  }
  
  .footer-input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
  }
  
  .footer-links {
      width: 100%;
      max-width: 1000px;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
  }
  
  .footer-link-wrapper {
    display: flex;
  }
  
  .footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* align-items: center; */
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;

  }
  
  .footer-link-items h2 {
    margin-bottom: 16px;
    position: relative;
  }

  .footer-link-items h2::after{
      content: "";
      position: absolute;
      height: 2px;
      background-color: white;
      width: 100%;
      left: 0;
      bottom: -5px;
  } 
  
  .footer-link-items > h2 {
    color: rgb(255, 166, 0);
  }
  
  .footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
  }
  
  .footer-link-items a:hover {
    /* color: #e9e9e9; */
    color: rgb(255, 166, 0);
    transition: 0.3s ease-out;
  }
  
  .footer-email-form h2 {
    margin-bottom: 2rem;
  }
  
  .footer-input::placeholder {
    color: #b1b1b1;
  }
  
  /* Social Icons */
  .social-icon-link {
    color: #fff;
    font-size: 24px;
  }
  
  .social-media {
    max-width: 1000px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 40px auto 0 auto;
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    margin-top: -20px;
  }
  
  .social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .website-rights {
    color: #fff;
    margin-bottom: 16px;
  }
  
  @media screen and (max-width: 820px) {
    .footer-links {
      padding-top: 2rem;
      /* flex-direction: column; */
      /* align-items: center; */
    }
  
    .footer-input {
      width: 100%;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
      align-items: center;
      /* flex-grow: 1; */
    }

    .footer-link-items{
        /* width: 100%; */
        gap: 5px
    }
  
    .social-media-wrap {
      flex-direction: column;
    }

    .social-icons{
        margin: 1rem 0 2rem;
    }
  }
  
  @media screen and (max-width: 768px) {
  }
  